* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  background-color: #fafafa;
  height: 100%;
  width: 100vw;
  overflow-y: scroll;
  max-height: 100vh;
}

.app__header {
  background-color: white;
  padding: 20px;
  border-bottom: 1px solid lightgrey;
  object-fit: contain;
  display: flex;
  justify-content: space-between;
}

.app__headerImage {
  object-fit: contain;
}

.contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 5vh;
}

.app__notify {
  padding: 15px;
  text-align: center;
}

.app__post_view {
  display: flex;
  justify-content: center;
  width: 100%;
}

.app__post_wrapper {
  max-width: 500px;
}

.app__loading {
  display: flex;
  justify-content: center;
  height: 2vh;
}

.app__bottom {
  text-align: center;
}